@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "PT Sans";
  font-display: swap;
  src: url("../fonts/ptsans.woff") format("woff"), url("../fonts/ptsans.woff2") format("woff2");
}
@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "PT Sans";
  font-display: swap;
  src: url("../fonts/ptsansbold.woff") format("woff"), url("../fonts/ptsansbold.woff2") format("woff2");
}
*,
::after,
::before {
  box-sizing: border-box;
}

html {
  box-sizing: border-box;
  background-color: #e7e5e5;
}

.body {
  display: flex;
  flex-direction: column;
  min-width: 320px;
  min-height: 100%;
  margin: 0 auto;
  font-size: 18px;
  line-height: 21px;
  font-family: "PT Sans", "Arial", sans-serif;
  color: #000000;
  background-color: #ffffff;
  box-shadow: 0 0 15px rgba(0, 0, 0, 0.2);
  max-width: 1200px;
}

.list-reset {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.img {
  max-width: 100%;
  object-fit: cover;
  object-position: center;
}

.button {
  display: block;
  width: 284px;
  margin: 0 auto;
  padding: 16px;
  font-weight: 700;
  font-size: 21px;
  line-height: 26px;
  font-family: "PT Sans", "Arial", sans-serif;
  text-align: center;
  color: #ffffff;
  text-transform: uppercase;
  text-decoration: none;
  background-color: #81b3d2;
  border: none;
}

.header {
  background: #ffffff;
  width: 100%;
}

.main-nav {
  position: relative;
  min-height: 56px;
}

.main-nav__logo {
  position: absolute;
  top: 0;
  left: 50%;
  display: block;
  width: 185px;
  height: 96px;
  transform: translateX(-50%);
}

.main-nav__container {
  display: none;
  position: absolute;
  z-index: 2;
  top: 0;
  left: 0;
  right: 0;
}
@media (min-width: 768px) {
  .main-nav__container {
    display: flex;
    justify-content: space-between;
    position: static;
  }
}

.main-nav__container::after {
  content: "";
  position: absolute;
  right: 0;
  bottom: 0;
  left: 0;
  height: 7px;
  background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.2) 100%);
}
@media (min-width: 768px) {
  .main-nav__container::after {
    display: none;
  }
}

.main-nav--open .main-nav__container {
  display: block;
  background-color: #81b3d2;
}

@media (min-width: 768px) {
  .main-nav__list {
    display: flex;
  }
}

.main-nav__item {
  text-align: center;
  padding: 17px 17px 13px;
  box-shadow: 0 1px 0 rgba(1, 1, 1, 0.2), inset 0 1px 0 rgba(255, 255, 255, 0.2);
}
@media (min-width: 768px) {
  .main-nav__item {
    width: 153px;
    box-shadow: none;
  }
}
@media (min-width: 1200px) {
  .main-nav__item {
    width: 240px;
  }
}

.main-nav__link {
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  color: #ffffff;
  text-transform: uppercase;
  text-decoration: none;
  display: inline-block;
}
@media (min-width: 768px) {
  .main-nav__link {
    color: #000000;
    font-size: 14px;
    line-height: 21px;
  }
}

.main-nav__link--active {
  color: #766357;
}

.main-nav__link:hover {
  color: #81b3d2;
}

.main-nav__link:active {
  color: rgba(0, 0, 0, 0.3);
}

.main-nav__toggle {
  position: absolute;
  top: 17px;
  right: 20px;
  width: 23px;
  height: 22px;
  background-image: url("../img/icon-menu.svg");
  background-repeat: no-repeat;
  border: none;
  z-index: 10;
  background-color: transparent;
  background-size: contain;
  cursor: pointer;
}
@media (min-width: 768px) {
  .main-nav__toggle {
    display: none;
  }
}

.main-nav--open .main-nav__toggle {
  background-image: url("../img/icon-cross.svg");
}

